import React, { useState, useEffect, useContext } from 'react';
import { Modal, Box, Typography, TextField, Button } from '@mui/material';
import { AppContext } from '../context/context';

const ModalCalendar = ({ open, handleCloseModal, item }) => {
  const { selectDate, admin, reservedSlots, setReservedSlots } = useContext(AppContext);

  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    phone: '',
    email: '',
    date: '',
    time: '',
  });

  useEffect(() => {
    if (item) {
      const selectedSlot = reservedSlots.find(
        (slot) => slot.date === selectDate.toLocaleDateString('fr-FR') && slot.time === item.time
      );

      if (selectedSlot) {
        setFormData(selectedSlot);
      } else {
        setFormData((prevData) => ({
          ...prevData,
          date: selectDate.toLocaleDateString('fr-FR') || '',
          time: item.time || '',
        }));
      }
    }
  }, [item, selectDate, reservedSlots]);

  const modalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 600,
    maxWidth: '95%',
    bgcolor: 'background.paper',
    boxShadow: 24,
    borderRadius: '14px',
    p: 4,
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!formData.firstName || !formData.lastName || !formData.phone || !formData.email) {
      alert('Veuillez remplir tous les champs.');
      return;
    }
    if (!/^[0-9]+$/.test(formData.phone)) {
      alert('Le numéro de téléphone doit contenir uniquement des chiffres.');
      return;
    }
    if (!/\S+@\S+\.\S+/.test(formData.email)) {
      alert("Veuillez entrer une adresse email valide.");
      return;
    }

    setReservedSlots((prevReservedSlots) => [
      ...prevReservedSlots,
      {
        date: formData.date,
        time: formData.time,
        email: formData.email,
        firstName: formData.firstName,
        lastName: formData.lastName,
        phone: formData.phone,
      },
    ]);

    handleModalClose();
  };

  const handleModalClose = () => {
    setFormData({
      firstName: '',
      lastName: '',
      phone: '',
      email: '',
      date: '',
      time: '',
    });
    handleCloseModal();
  };

  return (
    <Modal
      open={open}
      onClose={handleModalClose}
      aria-labelledby="modal-title"
      aria-describedby="modal-description"
    >
      <Box sx={modalStyle}>
        <Typography id="modal-title" variant="h6" component="h2" mb={2}>
          Détails du créneau
        </Typography>
        {admin ? (
          <Box mt={2}>
            <Typography variant="body1">
              <strong>Date :</strong> {formData.date}
            </Typography>
            <Typography variant="body1" mt={1}>
              <strong>Heure :</strong> {formData.time}
            </Typography>
            <Typography variant="body1" mt={2}>
              <strong>Prénom :</strong> {formData.firstName || 'Non renseigné'}
            </Typography>
            <Typography variant="body1" mt={1}>
              <strong>Nom :</strong> {formData.lastName || 'Non renseigné'}
            </Typography>
            <Typography variant="body1" mt={1}>
              <strong>Téléphone :</strong> {formData.phone || 'Non renseigné'}
            </Typography>
            <Typography variant="body1" mt={1}>
              <strong>Email :</strong> {formData.email || 'Non renseigné'}
            </Typography>
          </Box>
        ) : (
          <Box component="form" onSubmit={handleSubmit} mt={3}>
            <Typography variant="body1">
              <strong>Date :</strong> {formData.date}
            </Typography>
            <Typography variant="body1" mt={1}>
              <strong>Heure :</strong> {formData.time}
            </Typography>
            <TextField
              label="Prénom"
              name="firstName"
              value={formData.firstName}
              onChange={handleInputChange}
              fullWidth
              margin="normal"
              required
            />
            <TextField
              label="Nom"
              name="lastName"
              value={formData.lastName}
              onChange={handleInputChange}
              fullWidth
              margin="normal"
              required
            />
            <TextField
              label="Téléphone"
              name="phone"
              value={formData.phone}
              onChange={handleInputChange}
              fullWidth
              margin="normal"
              required
              inputProps={{ pattern: '[0-9]+' }}
            />
            <TextField
              label="Email"
              name="email"
              value={formData.email}
              onChange={handleInputChange}
              fullWidth
              margin="normal"
              required
              type="email"
            />
            <Button type="submit" variant="contained" color="primary" fullWidth sx={{ mt: 2 }}>
              Soumettre
            </Button>
          </Box>
        )}
      </Box>
    </Modal>
  );
};

export default ModalCalendar;
